import React, { useState } from "react";
import { Clock } from "../Clock";
import { FormBox, FormFieldset, Legend, FormLabel, FormInput, FormButton, Text, Error, Info } from "./styled";

export const Form = ({ calculateResult, ratesData }) => {
  const [amount, setAmount] = useState("");
  const [currency, setCurrency] = useState("GBP");



  const onFormSubmit = (event) => {
    event.preventDefault();
    calculateResult(currency, amount);
  }
  return (
    <FormBox onSubmit={onFormSubmit}>
      <Clock />
      <FormFieldset>

        <Legend>Uzupe≈Çnij dane:</Legend>
        {ratesData.state === "loading"
          ? (<Text>
            Poczekaj chwilkƒô... ≈Åadujƒô dane z Banku...
          </Text>
          )
          : (
            ratesData.state === "error" ? (
              <Error>
                Sprawd≈∫ po≈ÇƒÖczenie z internetem lub spr√≥buj ponownie.
              </Error>
            ) : (
                <>
                  <p>
                    <FormLabel htmlFor="amount" className="form__label">Podaj kwotƒô w z≈Çot√≥wkach*:</FormLabel>
                    <FormInput
                      value={amount}
                      onChange={({ target }) => setAmount(target.value)}
                      type="number" id="amount"
                      name="amount"
                      required
                    />
                  </p>
                  <p>
                    <FormLabel htmlFor="currency" className="form__label">Wybierz walutƒô:</FormLabel>
                    <FormInput as="select"
                      value={currency}
                      onChange={({ target }) => setCurrency(target.value)}
                      name="currency"
                      id="currency">
                      {Object.keys(ratesData.rates).map(((currency) => (
                        <option
                          key={currency}
                          value={currency}
                        >
                          {currency}
                        </option>)))}
                    </FormInput>
                  </p>
                  <p>
                    <FormButton>Przelicz</FormButton>
                  </p>
                  <Info>
                    Kursy walut pochodzƒÖ z Europejskiego Banku Centralnego.<br />
                    Aktualne na dzie≈Ñ: {ratesData.date}
                  </Info>
                </>))}


      </FormFieldset>
    </FormBox>
  )

}